<template>
   <div>
      <van-nav-bar
        class="backBar"
        title="我的收藏"
        left-arrow
        @click-left="onClickLeft"
      />

      <van-list
        class="vod-list"
        finished-text="没有更多了"
      >
        
        <van-swipe-cell v-for="(vod,index) of vods" :key="index">
            <div class="vod-class" @click="handleToDetail(vod.vodId)">
              <img v-lazy="vod.cover" class="fixed-size-image" />
              <div class="vod-info">
                <p class="name-class">{{ vod.vodName }}</p>
                <p class="year-class">{{ vod.year }}</p>
                <p class="director-class">{{ vod.director }}</p>
              </div>
            </div>
            <template #right>
                <van-button 
                  square text="删除" 
                  type="danger" 
                  class="delete-button"
                  @click="delClick(vod.vodId,index)"
                  />
            </template>
        </van-swipe-cell>

      </van-list>
   </div>
</template>

<script>
import { NavBar,List,SwipeCell,Button } from 'vant'
import 'vant/lib/nav-bar/style'
import 'vant/lib/list/style'
import 'vant/lib/button/style'
import 'vant/lib/swipe-cell/style'
import db from 'common/db.js'

export default {
  name: 'LikePage',
  components : {
    [NavBar.name]:NavBar,
    [List.name]:List,
    [SwipeCell.name]:SwipeCell,
    [Button.name]:Button
  },
  data () {
     return {
        vods:[]
     }
  },
  mounted () {
    console.log("lik--mounted")
  },
  activated() {
    // keep-live 当缓存的组件被激活时调用
    console.log("lik--activated")
    this.vods = []
    this.queryLike()
  },
  methods: {
    onClickLeft() {
      this.$router.back(-1)
    },
    queryLike() {
      db.vodlike.toArray().then((list)=>{
        console.log(list)
        this.vods.push(...list)
      })
    },
    delClick (vId,index) {
       console.log(vId)
       this.vods.splice(index,1)
       db.vodlike.delete(vId)
    },
    handleToDetail (id) {
      this.$router.push({ path: `/detail/${id}` })
    },
  }
}
</script>

